import { useForm } from "react-hook-form";
import { Link, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

import { Button } from "../../../Components";
import { useRecoveryMutation } from "../../../redux/API/authApiSlice";

import style from "../Login/login.module.scss";

const Recovery = () => {
  const [recovery] = useRecoveryMutation();
  const navigate = useNavigate();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = async (value) => {
    console.log(value);
    const id = toast.loading("    ");
    toast(
      "        .       !"
    );
    try {
      await recovery(value).unwrap();
      toast.update(id, {
        render: "     !",
        type: "success",
        isLoading: false,
        autoClose: 5000,
      });
      navigate("/login");
    } catch (error) {
      toast.update(id, {
        render: `${error.data.message} `,
        type: "error",
        isLoading: false,
        autoClose: 5000,
      });
    }
  };

  return (
    <div className="flex flex-col justify-center items-center h-[500px]">
      <form className={style.form} onSubmit={handleSubmit(onSubmit)}>
        <h1 className="text-black text-center text-3xl mb-6">
           
        </h1>
        <div className={style.form_group}>
          <div>
            <label>E-mail:</label>
            <input
              {...register("email", {
                required: true,
                validate: (value) => value !== "",
              })}
              type="email"
              placeholder="ivaninf@yandex.com"
            />

            {errors.email && <p>  </p>}
          </div>
          <Button auth> </Button>
        </div>

        <div className="flex justify-between text-white text-opacity-60 mt-4">
          <Link className="hover:text-green text-black" to="/login">
            
          </Link>
        </div>
      </form>
    </div>
  );
};

export default Recovery;
